import React from "react";
import ReactDOM from "react-dom";
import { HashRouter, Route, Redirect, Switch } from "react-router-dom";

// styles
import "assets/css/bootstrap.min.css";
import "assets/scss/paper-kit.scss";
import "assets/tamponia/tamponia.css"
// pages
import Home from "views/Home.js";
import About from "views/About.js";
import TermsAndConditions from "views/TermsAndConditions.js";
import PrivacyPolicy from "views/PrivacyPolicy.js";
import Disclaimer from "views/Disclaimer.js";

ReactDOM.render(
  // Use HashRouter for Github Pages
  <HashRouter basename="/">
    <Route render = {({ location }) => (
       <Switch>
          <Route
            exact path="/index"
            render={props => <Home {...props} />} />
          <Route
            exact path="/about"
            render={props => <About {...props} />}
          />
          <Route
            exact path="/terms_and_conditions"
            render={props => <TermsAndConditions {...props} />}
          />
          <Route
            exact path="/privacy_policy"
            render={props => <PrivacyPolicy {...props} />}
          />
          <Route
            exact path="/disclaimer"
            render={props => <Disclaimer {...props} />}
          />
          <Redirect to="/index" />
        </Switch>
     )} />
  </HashRouter>,
  document.getElementById("root")
);
